<template>
  <div class="container">
    <div class="hmmm-container">
      <el-card shadow="always">
        <!-- ####################################################################################################################################################### -->
        <div slot="header">
          <span></span>
        </div>

        <!-- ####################################################################################################################################################### -->
        <el-form label-width="120px"  ref="form" :model="form" :rules="rules">
          <!-- ..........................................................................  -->
          <el-form-item label="" prop="subjectID">
            <el-select
              placeholder=""
              style="width: 400px"
              v-model="form.subjectID"
              @change="subjectFn"
            >
              <el-option
                v-for="(item, index) in subjectList"
                :key="index"
                :value="item.value"
                :label="item.label"
              ></el-option>
            </el-select>
          </el-form-item>

          <!-- ............................................................................. -->
          <el-form-item label="" prop="catalogID">
            <el-select
              placeholder=""
              style="width: 400px"
              v-model="form.catalogID"
            >
              <el-option
                v-for="(item, index) in directorysimpleList"
                :key="index"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-form-item>

          <!-- ............................................................................. -->
          <el-form-item label="" prop="enterpriseID">
            <el-select
              placeholder=""
              style="width: 400px"
              v-model="form.enterpriseID"
            >
              <el-option
                v-for="(item, index) in companysList"
                :key="index"
                :label="item.company"
                :value="item.id"
              >
              </el-option>
            </el-select>
          </el-form-item>

          <!-- ................................................................................ -->
          <el-form-item label="" prop="province">
            <el-select
              placeholder=""
              style="width: 198px"
              v-model="form.province"
              :value="form.province"
              @change="provincesFn"
            >
              <el-option
                v-for="(item, index) in provincesList"
                :key="index"
                :label="item"
                :value="item"
              ></el-option>
            </el-select>
            <el-select
              placeholder=""
              style="width: 198px; margin-left: 4px"
              v-model="form.city"
            >
              <el-option
                :value="item"
                v-for="(item, index) in citysList"
                :key="index"
                :label="item"
              ></el-option>
            </el-select>
          </el-form-item>

          <!-- ................................................................................ -->
          <el-form-item label="" prop="direction">
            <el-select
              placeholder=""
              style="width: 400px"
              v-model="form.direction"
            >
              <el-option
                :label="item"
                :value="item"
                v-for="item in directionList"
                :key="item.id"
              ></el-option>
            </el-select>
          </el-form-item>

          <!-- ................................................................................ -->
          <el-form-item label="" prop="questionType">
            <el-radio-group v-model="form.questionType" @change="RadioChangeFn">
              <el-radio label="1"></el-radio>
              <el-radio label="2"></el-radio>
              <el-radio label="3"></el-radio>
            </el-radio-group>
          </el-form-item>

          <!-- ................................................................................ -->
          <el-form-item label="" prop="difficulty">
            <el-radio-group v-model="form.difficulty">
              <el-radio label="1"></el-radio>
              <el-radio label="2"></el-radio>
              <el-radio label="3"></el-radio>
            </el-radio-group>
          </el-form-item>

          <!-- ................................................................................ -->
          <el-form-item label="" prop="question">
            <quill-editor
              @blur="$refs.form.validateField('question')"
              v-model="form.question"
              :options="editorOption"
              ref="myQuillEditor"
            ></quill-editor>
          </el-form-item>

          <!-- ................................................................................ -->
          <el-form-item label="" v-show="form.questionType !== '3'">
            <el-form-item
              v-model="form.options"
              v-for="(item, index) in form.zimu"
              :key="index"
              style="padding-bottom: 20px"
            >
              <el-radio
                :label="index"
                v-show="form.questionType === '1'"
                style="margin-right: 0px"
                v-model="form.radioA"
                >{{ item.code }}</el-radio
              >
              <el-checkbox
                v-show="form.questionType === '2'"
                v-model="form.zimu[index].isRight"
                :label="index"
                style="margin-right: 0px"
                >{{ item.code }}</el-checkbox
              >
              <el-input style="width: 240px" v-model="item.title"></el-input>
              <el-upload
                v-model="form.upload"
                class="avatar-uploader"
                action="https://jsonplaceholder.typicode.com/posts/"
                :show-file-list="false"
                :on-success="handleAvatarSuccess"
                :before-upload="beforeAvatarUpload"
              >
                <span></span>
                <img v-if="imageUrl" :src="imageUrl" class="avatar" />
                <i v-else class="el-icon-circle-close"></i>
              </el-upload>
            </el-form-item>

            <!-- ................................................................. -->
            <el-button
              type="danger"
              :disabled="form.questionType !== '2'"
              v-model="form.addOption"
              size="small"
              @click="AddAnswer"
              >+</el-button
            >
          </el-form-item>

          <!-- ......................................................................... -->
          <el-form-item label="">
            <el-input style="width: 400px" v-model="form.videoURL"></el-input>
          </el-form-item>

          <!-- ......................................................................... -->
          <el-form-item label="" prop="answer">
            <quill-editor
              @blur="$refs.form.validateField('answer')"
              v-model="form.answer"
              ref="myQuillEditor"
              :options="editorOption"
            >
            </quill-editor>
          </el-form-item>

          <!-- .......................................................................... -->
          <el-form-item label="">
            <el-input
              :rows="4"
              type="textarea"
              style="width: 400px"
              v-model="form.remarks"
            ></el-input>
          </el-form-item>

          <!-- ...........................................................................  -->
          <el-form-item label="">
            <el-select
              multiple
              filterable
              allow-create
              default-first-option
              placeholder=""
              v-model="form.tags"
              style="width: 400px"
            >
              <el-option
                v-for="(item, index) in tagsList"
                :key="index"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-form-item>

          <!-- ............................................................................ -->
          <el-form-item>
            <el-button :type="ButtonColor" @click="preSentFn">{{
              form.SureSubmit
            }}</el-button>
          </el-form-item>

        </el-form>
      </el-card>
    </div>
  </div>
</template>

<script>

// 
import { simple as getSubject } from '@/api/hmmm/subjects'
//    
import { add as getMoreDirectory, detail as getDirectoryDetail, update as UpdateDirectory } from '@/api/hmmm/questions'
// 
import { simple as getdirectorysimpleList } from '@/api/hmmm/directorys'
// 
import { list as getCompanysList } from '@/api/hmmm/companys'
// 
import { provinces as getProvincesList, citys as getCitysList } from '@/api/hmmm/citys'
// 
import { direction as getDirection } from '@/api/hmmm/constants'
// 
import { simple as getTagesList } from '@/api/hmmm/tags'

export default {
  data () {
    return {
      test: false,
      id: '',
      //   
      isShow: true,
      IsDisabled: 'false',
      // 
      editorOption: {
        modules: {
          toolbar: [
            ['bold', 'italic', 'underline', 'strike'], // 
            [{ list: 'ordered' }, { list: 'bullet' }], // 
            ['blockquote'], // 
            ['code-block', 'image', 'link'], //   
            [{ align: [] }]
          ]
        },
        // 
        placeholder: ''
      },
      // 
      zimuMore: 69,
      form: {
        id: '',
        options: [],
        SureSubmit: '',
        zimu: [
          { code: 'A', title: '', img: '', isRight: false },
          { code: 'B', title: '', img: '', isRight: false },
          { code: 'C', title: '', img: '', isRight: false },
          { code: 'D', title: '', img: '', isRight: false }
        ],
        // //
        upload: '', // 
        Ischecked: [], //
        answer: '', // 
        question: '', // 
        videoURL: '', // 
        remarks: '', // 
        subjectID: '', // 
        direction: '', // 
        enterpriseID: '', // 
        questionType: '1', // 
        radioChange: 1, //
        difficulty: '1', // 
        province: '', // 
        city: '', // 
        catalogID: '', // 
        radioA: '', //
        tags: [], // 
        addOption: '', //
        radioBB: '' //
      },
      subjectList: [], // 
      directorysimpleList: [], // 
      // 
      directionList: getDirection,
      companysList: [], // 
      // 
      provincesList: getProvincesList(),
      citysList: [],
      imageUrl: '',
      listList: [],
      tagsList: [],
      rules: {
        subjectID: [
          {
            required: true,
            message: '',
            trigger: 'change'
          }
        ],
        catalogID: [
          { required: true, message: '', trigger: 'change' }
        ],
        enterpriseID: [
          { required: true, message: '', trigger: 'change' }
        ],
        province: [
          { required: true, message: '', trigger: 'change' }
        ],
        direction: [
          { required: true, message: '', trigger: 'change' }
        ],
        questionType: [{ required: true, message: '' }],
        difficulty: [{ required: true, message: '' }],
        question: [
          {
            required: true,
            message: '',
            trigger: ['blur', 'change']
          }
        ],
        answer: [
          {
            required: true,
            message: '',
            trigger: ['blur', 'change']
          }
        ]
      }
    }
  },

  // 
  async created () {
    this.ButtonColor = 'primary'
    const res = await getSubject(this.form)
    // console.log(res.data)
    // 
    this.subjectList = res.data
    // console.log(this.subjectList)
    // 
    const res2 = await getCompanysList()
    // console.log(res2.data)
    this.companysList = res2.data.items
    await this.ChangeQuestions()
  },

  methods: {
    // 
    AddAnswer () {
      // 
      this.form.zimu.push({
        code: String.fromCharCode(this.zimuMore),
        title: '',
        img: '',
        isRight: false
      })
      // console.log(this.form.zimu)
      this.zimuMore++
    },
    //    
    RadioChangeFn (value) {
      console.log(value)
      if (value === '1') {
        //  
        this.isShow = true
        // 1   
        this.form.radioChange = 1
        //   
        this.form.Ischecked = []
      } else if (value === '2') {
        //  
        this.isShow = true
        // 2   
        this.form.radioChange = 2
        //   
        this.form.radioA = ''
      } else if (value === '3') {
        //  
        this.isShow = false
      }
    },

    //   // 
    async subjectFn (e) {
      console.log(e)
      // 
      const res = await getdirectorysimpleList({
        subjectID: this.form.subjectID
      })
      console.log(res.data)
      this.directorysimpleList = res.data
      this.form.catalogID = ''
      // 
      const res1 = await getTagesList({ subjectID: this.form.subjectID })
      console.log(res1.data)
      this.tagsList = res1.data
      this.form.tags = ''
    },
    // 
    async provincesFn (e) {
      console.log(e)
      const res = await getCitysList(e)
      this.citysList = res
      this.form.city = ''
    },
    handleAvatarSuccess (res, file) {
      this.imageUrl = URL.createObjectURL(file.raw)
    },
    beforeAvatarUpload (file) {
      const isJPG = file.type === 'image/jpeg'
      const isLt2M = file.size / 1024 / 1024 < 2
      if (!isJPG) {
        this.$message.error(' JPG !')
      }
      if (!isLt2M) {
        this.$message.error(' 2MB!')
      }
      return isJPG && isLt2M
    },
    // 
    async preSentFn () {
      // console.log(this.form.company)
      // id 
      const obj = {
        subjectID: this.form.subjectID, // 
        catalogID: this.form.catalogID, // 
        enterpriseID: this.form.enterpriseID, // 
        province: this.form.province, // 
        city: this.form.city, // 
        direction: this.form.direction, // 
        questionType: this.form.questionType, // 
        difficulty: this.form.difficulty, // 
        question: this.form.question, // 
        options: this.form.options, // 
        videoURL: this.form.videoURL, // 
        answer: this.form.answer, // 
        remarks: this.form.remarks // 
      }
      await this.$refs.form.validate()
      if (this.$route.query.id) {
        await UpdateDirectory({
          ...obj,
          id: this.form.id,
          tags: this.form.tags ? this.form.tags.join(',') : this.form.tags// 
        })
        this.$message.success('')
        this.$router.push({ path: '/questions/list' })
      } else {
        // id 
        await getMoreDirectory({
          ...obj,
          tags: this.form.tags ? this.form.tags.join(',') : this.form.tags // 
        })
        this.$message.success('')
        setTimeout(() => {
          this.$router.push({ path: '/questions/list' })
        }, 1500)
      }
    },
    // 
    async ChangeQuestions () {
      if (this.$route.query.id) {
        const res = await getDirectoryDetail({ id: this.$route.query.id })
        // console.log(res.data)
        this.form.id = res.data.id
        // 
        this.form = res.data
        // id
        this.form.subjectID = res.data.subjectID
        const res2 = await getdirectorysimpleList({
          subjectID: this.form.subjectID
        })
        // 
        // console.log(res2.data)
        this.directorysimpleList = res2.data
        // 
        this.form.catalogID = res.data.catalogID
        // 
        const res1 = await getTagesList({ subjectID: this.form.subjectID })
        this.tagsList = res1.data
        this.form.tags = res.data.tags.split(',')
        // 
        this.form.zimu = res.data.options
        if (res.data.questionType === '1') {
          this.isShow = true
          this.form.radioChange = 1
        }
        if (res.data.questionType === '2') {
          // console.log(res.data)
          this.form.zimu = res.data.options
          this.form.zimu.forEach((item) => {
            if (item.isRight === 1) item.isRight = true
          })
          this.isShow = true
          this.form.radioChange = 2
        } else {
          this.isShow = false
        }
        this.testQuestionsEnter = ''
        this.form.SureSubmit = ''
        this.ButtonColor = 'success'
      }
    }
  }

}
</script>

<style scoped lang="scss">
.hmmm-container{
  margin: 20px 20px;
}
.quill-editor {
  ::v-deep .ql-editor {
    height: 200px;
  }
}
.avatar-uploader {
  display: inline-block;
  margin-left: 4px;
  vertical-align: middle;
}
::v-deep .avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  width: 100px;
  height: 60px;
  line-height: 60px;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader i {
  position: absolute;
  right: 0;
  top: 0;
  -webkit-transform: translate(50%, -50%);
  transform: translate(50%, -50%);
  background: #fff;
  font-size: 18px;
  color: #999;
}
.avatar {
  width: 100px;
  height: 60px;
  display: block;
}

::v-deep .el-tag.el-tag--info {
  color: rgb(34, 32, 33);
  // background: linear-gradient(to left, #e7f27f, rgb(199, 237, 12), #fff222);
}
// ::v-deep .el-select-dropdown__item.hover,
// .el-select-dropdown__item:hover {
//   // background-color: #fff222;
// }
</style>
